@import url('https://fonts.googleapis.com/css2?family=Alata&family=Crimson+Text&family=Montserrat:ital,wght@0,300;1,600&display=swap');

.skill{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 200px 0;
}

.skill_text{
    color: white;
    flex: 0.6;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
}

.skill_text>h2{
    font-size: 30px;
}

.skill_img{
    flex: 0.4;
    text-align: center;
    margin-right: 100px;
}

.skill_img>img{
    width: 300px;
    border-radius: 200px;
    object-fit: contain;
}

.skill_skillset>h5{
    font-family: "Montserrat" , sans-serif;
    color: orangered;
    padding: 20px 0;
}

