.exp{
    display: flex;
    flex-direction: column;
    color: wheat;
    justify-content: center;
    margin: 200px 0;
    align-items: center;
}

.exp_info{
    background: url("https://img.freepik.com/free-photo/abstract-space-wallpaper-background-dark-smoke-design_53876-128279.jpg");
    background-position: center;
    background-size: cover;
    height: 300px;
    z-index: 1000px;
    width: 80vw;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.9);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}