.expb{
    display: flex;
    flex-direction: column;
    border: 2px dotted white;
    border-radius: 10px;
    width: 200px;
    height: 150px;
    justify-content: center;
    align-items: center;
    background-color: black;
    overflow: hidden;

}

.expb>p{
    font-family: sans-serif;
    font-size: 15px;
    letter-spacing: 1px;
    align-items: center;
}

.expb>h1{
    align-items: center;
}
