.proj>img{
    width: 350px;
    height: 170px;
}

.proj{
    margin: 15px;
    width: 350px;
    height: 170px;
    transition: tranform 100ms ease-in;
}

.proj:hover{
    color: white;
    background-color: orangered;
    transform: scale(1.05);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.projcc{
    padding: 10px;
}

.projcc>h4{
    padding-bottom: 5px;
}


.projcc>p{
    font-size: 15px;
}